import React, { Component } from "react";

class Demo extends Component {
    constructor() {
        super()
        this.state = {
            message: '3001',
            origin: 'http://192.168.1.186:3001'
        }
    }
    componentDidMount() {
        window.ABC3001 = function () { }
        window.addEventListener("message", (event) => {
            const { data, origin } = event;
            if ("http://192.168.1.186:3000" !== origin) return;
            console.log("3001 receive:", data, origin);
            this.setState({ message: data, origin })
        });
    }
    handleSendClick = () => {
        var a = window.parent.ABC3000
        console.log('react.3001.click', a);
        window.parent.postMessage({ a: 1 }, 'http://192.168.1.186:3000/#/')
        this.setState({
            message: '3001',
            origin: 'http://192.168.1.186:3001'
        })
    }
    render() {
        return (
            <div>
                <button onClick={this.handleSendClick}>3001 send message</button>
                <p>{this.state.message}</p>
                <p>{this.state.origin}</p>
            </div>
        )
    }
}
export default Demo